/**
 * 优惠券配置
 */ 
/**
 * 优惠券类型
	1：代金券（满减）
	2：折扣券（满折）
 */
const couponType={
	cash:1,
	dis:2
}
/**
 * 有效期方式
	1：固定日期生效失效
	2：领取后N天生效N天失效
 */
const expType={
	fixed:1,
	adapt:2
}
/**
 * 领取记录状态
 * 1 正常
 * 2 已核销
 * 0 禁用,失效,过期
 */
const status={
	used:1,
	disabled:0,
	hasUsed:2
}
export default{
	couponType,
	expType,
	status
}